// Typography
//
// Headings, body text, lists, and other misc typographic elements.

h1, h2, h3, h4, h5, h6 {
  margin-bottom: .5rem;
  font-weight: 600;
  line-height: 1.25;
  color: var(--heading-color);
}

h1 {
  font-size: 2rem;
}

h2 {
  margin-top: 1rem;
  font-size: 1.5rem;
}

h2[id] {
  scroll-margin-top: 70px; /* masthead 높이보다 살짝 여유 있게 */
}


@media (max-width: 600px) {
  .page-title, .post-title, item.title, h1, h2, h3, h4, h5, h6 {
    text-align: center;
    word-break: keep-all;      /* 단어를 가능한 한 유지 */
    white-space: normal;       /* 자연스럽게 줄바꿈 */
    overflow-wrap: break-word; /* 단어가 너무 길면 단어 단위로 줄바꿈 */
    line-height: 1.4;
  }

  h2[id] {
    scroll-margin-top: 80px !important; /* 모바일 환경에서는 더 여유롭게 */
  }
}

h3 {
  margin-top: 1.5rem;
  font-size: 1.25rem;
}

h4, h5, h6 {
  margin-top: 1rem;
  font-size: 1rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

p_lowBtMargin {
  margin-top: 0;
  margin-bottom: 0.25rem;
  font-weight: bold;
}

ul, ol, dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

dt {
  font-weight: bold;
}

dd {
  margin-bottom: .5rem;
}

hr {
  position: relative;
  margin: 0.5rem 0;
  // margin: var(--spacer-2) 0;
  border: 0;
  border-top: 1px solid var(--border-color);
}

abbr {
  font-size: 85%;
  font-weight: bold;
  color: var(--gray-600);
  text-transform: uppercase;

  &[title] {
    cursor: help;
    border-bottom: 1px dotted var(--border-color);
  }
}

blockquote {
  padding: .5rem 1rem;
  margin: .8rem 0;
  color: var(--gray-500);
  border-left: .25rem solid var(--border-color);

  p:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 30em) {
    padding-right: 5rem;
    padding-left: 1.25rem;
  }
}

figure {
  margin: 0;
}

// main{
//   padding-top: 60px;  /* 네비게이션 높이에 맞게 여백 추가 */
// }

article {
  margin-bottom: 1.75rem; // 원하는 값으로 조절 (예: 2rem, 1rem 등)
}

// Markdown footnotes
//
// See the example content post for an example.

// Footnote number within body text
a[href^="#fn:"],
// Back to footnote link
a[href^="#fnref:"] {
  display: inline-block;
  margin-left: .1rem;
  font-weight: bold;
}

// List of footnotes
.footnotes {
  margin-top: 2rem;
  font-size: 85%;
}

// Custom type
//
// Extend paragraphs with `.lead` for larger introductory text.
.lead {
  font-size: 1.25rem;
  font-weight: 300;
}